.contactSection {
  margin-top: 8%;
  .contactContainer {
    display: flex;
    flex-direction: column;
    align-items:center; 
    
    a {
      width: 80%;
      
      .contactIcons {
        padding: 30px 0px;
        margin: 1% 0%;
        border-radius: 25px;
        color: #fff;
        font-size: 50px;
        background: $millbrook;
        transition-duration: 300ms;
        &:hover {
          background: black;
          color: white;
          
        }
      }
    }
  }
}
